				console.log('ÂºÄÂßãËé∑ÂèñÂæÆ‰ø°Áî®Êà∑‰ø°ÊÅØ...');
				console.log('ÂΩìÂâçËøêË°åÁéØÂ¢É:', uni.getSystemInfoSync());
				
