				});
			});
		},
		
		// Ëé∑ÂèñÂæÆ‰ø°ÁôªÂΩïÂá≠ËØÅ
		getWechatLoginCode() {
			return new Promise((resolve, reject) => {
				console.log('ÂºÄÂßãË∞ÉÁî®uni.login...');
				console.log('ÂΩìÂâçËøêË°åÁéØÂ¢É:', uni.getSystemInfoSync());
				
				// Ê£ÄÊü•ÊòØÂê¶ÊîØÊåÅuni.login
