					uni.showToast({
						title: 'ÂΩìÂâçÁéØÂ¢É‰∏çÊîØÊåÅÂæÆ‰ø°ÁôªÂΩï',
						icon: 'none'
					});
					reject(new Error('‰∏çÊîØÊåÅuni.login'));
					return;
				}
				
				uni.login({
					provider: 'weixin',
					success: async (loginRes) => {
						try {
							console.log('uni.loginÊàêÂäü:', loginRes);
							console.log('ÁôªÂΩïcodeÈïøÂ∫¶:', loginRes.code ? loginRes.code.length : 0);
							console.log('ÁôªÂΩïcodeÂâç10‰Ωç:', loginRes.code ? loginRes.code.substring(0, 10) + '***' : 'null');
							
							console.log('ÂºÄÂßãË∞ÉÁî®ÂæÆ‰ø°ÁôªÂΩï‰∫ëÂáΩÊï∞...');
							const result = await uniCloud.callFunction({
								name: 'wechat-login',
								data: { code: loginRes.code }
							});
							
							console.log('ÂæÆ‰ø°ÁôªÂΩï‰∫ëÂáΩÊï∞ËøîÂõûÁªìÊûú:', result);
							console.log('‰∫ëÂáΩÊï∞resultÂ≠óÊÆµ:', result.result);
							
							if (result.result && result.result.code === 0) {
								resolve(result.result);
							} else {
								console.error('ÂæÆ‰ø°ÁôªÂΩï‰∫ëÂáΩÊï∞ËøîÂõûÈîôËØØ:', result.result);
								reject(new Error(result.result ? result.result.message : 'ÂæÆ‰ø°ÁôªÂΩïÂ§±Ë¥•'));
							}
						} catch (error) {
							console.error('Ë∞ÉÁî®‰∫ëÂáΩÊï∞Â§±Ë¥•:', error);
							console.error('ÈîôËØØËØ¶ÊÉÖ:', {
								message: error.message,
								stack: error.stack,
								platform: uni.getSystemInfoSync().platform
							});
							reject(error);
						}
					},
					fail: (error) => {
						console.error('uni.loginÂ§±Ë¥•:', error);
						console.error('ÁôªÂΩïÂ§±Ë¥•ËØ¶ÊÉÖ:', {
							errMsg: error.errMsg,
							errCode: error.errCode,
							platform: uni.getSystemInfoSync().platform
						});
						
						// ÈíàÂØπÁúüÊú∫ÁöÑÁâπÊÆäÂ§ÑÁêÜ
						if (error.errMsg && error.errMsg.includes('deny')) {
							uni.showToast({
								title: 'Áî®Êà∑ÊãíÁªùÁôªÂΩïÔºåËØ∑Âú®ÂæÆ‰ø°‰∏≠ÈáçÊñ∞ÊéàÊùÉ',
								icon: 'none',
								duration: 3000
							});
						} else {
							uni.showToast({
								title: 'ÂæÆ‰ø°ÁôªÂΩïÂ§±Ë¥•',
								icon: 'none'
							});
						}
						reject(error);
					}
				});
			});
		},
		
		// ‰∏ÄÈîÆÁôªÂΩï
		async handleQuickLogin() {
			try {
				uni.showLoading({ title: 'ÁôªÂΩï‰∏≠...' });
				
				// Ë∞ÉÁî®‰∏ÄÈîÆÁôªÂΩï‰∫ëÂáΩÊï∞
				const result = await uniCloud.callFunction({
					name: 'quick-login',
					data: {}
