					await this.handleUnifiedLogin('quick', result.result.userInfo);
				} else {
					uni.showToast({
						title: result.result.message || '‰∏ÄÈîÆÁôªÂΩïÂ§±Ë¥•',
						icon: 'none'
					});
				}
			} catch (error) {
				console.error('‰∏ÄÈîÆÁôªÂΩïÂ§±Ë¥•:', error);
				uni.showToast({
					title: 'ÁôªÂΩïÂ§±Ë¥•ÔºåËØ∑ÈáçËØï',
					icon: 'none'
				});
			} finally {
				uni.hideLoading();
			}
		},
		
		// Ë¥¶Âè∑ÂØÜÁ†ÅÁôªÂΩï
		async handlePasswordLogin() {
			if (!this.passwordLoginForm.username || !this.passwordLoginForm.password) {
				uni.showToast({
					title: 'ËØ∑ËæìÂÖ•Áî®Êà∑ÂêçÂíåÂØÜÁ†Å',
					icon: 'none'
