				return;
			}
			
			try {
				uni.showLoading({ title: 'ÁôªÂΩï‰∏≠...' });
				
				const result = await uniCloud.callFunction({
					name: 'password-login',
					data: this.passwordLoginForm
				});
				
				if (result.result.code === 0) {
					await this.handleUnifiedLogin('password', result.result.userInfo);
					this.showPasswordLogin = false;
					this.passwordLoginForm = { username: '', password: '' };
				} else {
					uni.showToast({
						title: result.result.message || 'ÁôªÂΩïÂ§±Ë¥•',
						icon: 'none'
					});
				}
			} catch (error) {
				console.error('Ë¥¶Âè∑ÂØÜÁ†ÅÁôªÂΩïÂ§±Ë¥•:', error);
				uni.showToast({
					title: 'ÁôªÂΩïÂ§±Ë¥•ÔºåËØ∑ÈáçËØï',
					icon: 'none'
				});
			} finally {
				uni.hideLoading();
			}
		},
		
		// ÂèëÈÄÅÁü≠‰ø°È™åËØÅÁ†Å
		async sendSmsCode() {
			if (!this.smsLoginForm.phone) {
				uni.showToast({
					title: 'ËØ∑ËæìÂÖ•ÊâãÊú∫Âè∑',
					icon: 'none'
				});
				return;
			}
			
			try {
				const result = await uniCloud.callFunction({
					name: 'send-sms-code',
					data: { phone: this.smsLoginForm.phone }
				});
				
				if (result.result.code === 0) {
					uni.showToast({
						title: 'È™åËØÅÁ†ÅÂ∑≤ÂèëÈÄÅ',
						icon: 'success'
					});
					this.startSmsCountdown();
				} else {
					uni.showToast({
						title: result.result.message || 'ÂèëÈÄÅÂ§±Ë¥•',
						icon: 'none'
					});
				}
			} catch (error) {
				console.error('ÂèëÈÄÅÈ™åËØÅÁ†ÅÂ§±Ë¥•:', error);
				uni.showToast({
					title: 'ÂèëÈÄÅÂ§±Ë¥•ÔºåËØ∑ÈáçËØï',
					icon: 'none'
				});
			}
		},
		
		// ÂºÄÂßãÁü≠‰ø°ÂÄíËÆ°Êó∂
		startSmsCountdown() {
			this.smsCountdown = 60;
			const timer = setInterval(() => {
				this.smsCountdown--;
				if (this.smsCountdown <= 0) {
					clearInterval(timer);
				}
			}, 1000);
		},
		
		// ÊâãÊú∫È™åËØÅÁ†ÅÁôªÂΩï
		async handleSmsLogin() {
			if (!this.smsLoginForm.phone || !this.smsLoginForm.code) {
				uni.showToast({
					title: 'ËØ∑ËæìÂÖ•ÊâãÊú∫Âè∑ÂíåÈ™åËØÅÁ†Å',
					icon: 'none'
				});
				return;
			}
			
			try {
				uni.showLoading({ title: 'ÁôªÂΩï‰∏≠...' });
				
				const result = await uniCloud.callFunction({
					name: 'sms-login',
					data: this.smsLoginForm
				});
				
				if (result.result.code === 0) {
					await this.handleUnifiedLogin('sms', result.result.userInfo);
					this.showSmsLogin = false;
					this.smsLoginForm = { phone: '', code: '' };
				} else {
					uni.showToast({
						title: result.result.message || 'ÁôªÂΩïÂ§±Ë¥•',
						icon: 'none'
					});
				}
			} catch (error) {
				console.error('ÊâãÊú∫È™åËØÅÁ†ÅÁôªÂΩïÂ§±Ë¥•:', error);
				uni.showToast({
					title: 'ÁôªÂΩïÂ§±Ë¥•ÔºåËØ∑ÈáçËØï',
					icon: 'none'
				});
			} finally {
				uni.hideLoading();
			}
		},
		
		// Áªü‰∏ÄÁôªÂΩïÂ§ÑÁêÜ
		async handleUnifiedLogin(loginType, userData) {
			try {
				console.log('ÂºÄÂßãÁªü‰∏ÄÁôªÂΩïÂ§ÑÁêÜ:', { loginType, userData });
				
				// Ë∞ÉÁî®Áªü‰∏ÄÁôªÂΩï‰∫ëÂáΩÊï∞
				const result = await uniCloud.callFunction({
					name: 'unified-login',
					data: {
						loginType,
						userData
					}
				});
				
				console.log('Áªü‰∏ÄÁôªÂΩï‰∫ëÂáΩÊï∞ËøîÂõûÁªìÊûú:', result);
				
				if (result.result && result.result.code === 0) {
					// ‰øùÂ≠òÁôªÂΩï‰ø°ÊÅØ
					uni.setStorageSync('userInfo', result.result.userInfo);
					uni.setStorageSync('token', result.result.token);
					uni.setStorageSync('uid', result.result.userInfo.uid); // Â≠òÂÇ®uni-idÁöÑuid
					uni.setStorageSync('loginType', loginType);
					
					console.log('ÁôªÂΩï‰ø°ÊÅØÂ∑≤‰øùÂ≠òÂà∞Êú¨Âú∞Â≠òÂÇ®');
					
					uni.showToast({
