				}
			} catch (error) {
				console.error('Áªü‰∏ÄÁôªÂΩïÂ§ÑÁêÜÂ§±Ë¥•:', error);
				uni.showToast({
					title: 'ÁôªÂΩïÂ§±Ë¥•ÔºåËØ∑ÈáçËØï',
					icon: 'none'
				});
				return false;
			}
		},
		
		// ÊµãËØïË∑≥ËΩ¨ÊñπÊ≥ï
		testJump() {
